<template>
  <div class="user-profile-update">
    <router-view></router-view>
    <!--<div class="row">
      <span style="font-size: 17px;"><b> Profile Settings </b></span>
    </div>-->
    <div class="row" style="padding:0; margin: 0; width: 100%;">
      <div class="col-lg-12 reset-col">
        <div class="card" style="padding-bottom: 5px; border-top: 3px solid #03A9F4">
          <div class="row">
            <ul class="nav nav-tabs" style="width: 100%">
              <li class="nav-item">
                <a class="nav-link active" href="#" style="border-radius:0; font-size:13px; padding-right: 10px; padding-left: 10px;"><strong>Profile</strong></a>
              </li>
            </ul>
          </div>
          <div class="row">
            <div class="col-lg-12 reset-col" style="margin-top: 15px;">
              <div class="row user-row">
                <div class="col-lg-4">
                  <span class="span-info"><b>User Photo</b></span>
                </div>
                <div class="col-lg-8">
                  <img :src="photo" alt="user" class="img-thumbnail" style="max-height:85px; max-width: 85px; width: 85px; height:85px;">
                  <div class="row">
                    <router-link :to="{name: 'UpdatePhoto'}"><button class="upload-pad-top btn btn-info btn-raised"><b>Update Photo</b></button></router-link>
                  </div>
                </div>
              </div>
              <div class="row user-pad-row" style="border-top: 1px solid #ccc; margin-top:15px;">
                <div class="col-lg-4">
                  <span class="span-info"><b>Account Information</b></span>
                </div>
                <div class="col-lg-8">
                  <div class="row">
                    <strong>Display Name</strong>
                  </div>
                  <el-input class="input-pad-top input-pad-bottom" placeholder="Display Name" v-model="displayName"></el-input>
                  <div class="row">
                    <a href="#" class="input-pad" @click="updateDisplayName"><b>Update Display Name</b></a>
                  </div>
                </div>
              </div>

              <div class="row">
                <div class="col-lg-8 offset-lg-4">
                  <div class="row">
                    <strong>Email</strong>
                  </div>
                  <el-input class="input-pad-top input-pad-bottom" placeholder="Email" v-model="email" disabled>

                  </el-input>
                </div>
              </div>
              <div class="row user-row user-pad-row" style="border-top: 1px solid #ccc;">
                <div class="col-lg-4">
                  <span class="span-info"><b>Password</b></span>
                </div>
                <div class="col-lg-8">
                  <router-link :to="{name: 'passwordReset'}"><b>Reset Password</b></router-link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style>
  .span-info {
    font-size:13px;
    color: #93a3b5;
  }
  @media (min-width: 576px) {
    div.user-profile-update div.el-dialog__body .container {
      width:540px;
    }
  }

  @media (min-width: 700px) {
    div.user-profile-update div.el-dialog__body .container {
      width:700px;
    }
  }

  .label-info {
    font-size: 12px;
    font-weight: lighter;
  }

  div.el-dialog__body .container {
    position: relative;
    margin-left: auto;
    margin-right: auto;
  }

  .row-table {
    display: table;
  }

  .input-pad-top {
    margin-top:5px;
  }

  .upload-pad-top {
    margin-top: 10px;
    padding: 5px;
  }

  div.user-profile-update .el-date-editor.el-input, div.user-profile-update .el-input {
    width: 75%;
  }

  div.user-profile-update ul.el-upload-list.el-upload-list--text {
    display:none;
  }


  .input-pad-bottom {
    margin-bottom:8px;
  }

  div.user-row {
    margin-top:15px;
  }

  div.user-pad-row {
    padding-top:15px;
    padding-bottom:15px;
  }

  .col-cell {
    display: table-cell;
  }

</style>
<script>
  import fireBase from '../../../firebase/vueFire'
  import { Message } from 'element-ui'
  export default {
    name: 'userProfile',
    data () {
      return {
        dialogVisible: false,
        birthDate: null,
        displayName: this.$store.getters.getUser.nickName,
        email: this.$store.getters.getUser.email,
        file: {
          url: '',
          obj: null
        },
      }
    },
    computed: {
      photo () {
        return this.$store.getters.getUserImage
      }
    },
    methods: {
      onFileSelected (file) {
        this.file.obj = file
        this.file.url = file.name
        console.log(file)
        return false
      },
      updateDisplayName () {
        var user = fireBase.auth().currentUser
        user.updateProfile({
          displayName: this.displayName
        }).then(() => {
          Message({
            showClose: true,
            message: 'Your display name has been successfully updated.',
            type: 'success'
          })
          this.$store.dispatch('setUser', user)
        }, () => {
          Message({
            showClose: true,
            message: 'There was an error when trying to update your display name.',
            type: 'error'
          })
        })
      },
      saveProfile () {
        var user = fireBase.auth().currentUser
        user.updateProfile({
          dob: this.birthDate
        }).then(() => {
          Message({
            showClose: true,
            message: 'Your profile has been successfully updated.',
            type: 'success'
          })
          console.log(fireBase.auth().currentUser)
        }, () => {
          Message({
            showClose: true,
            message: 'There was an error when trying to update your profile.',
            type: 'error'
          })
        })
      }
    },
    mounted () {
      this.$ga.trackPage(this.$route.path)
    }
  }
</script>



